.nav-account{
    padding: 15px;  
    // min-width: 300px;
    // max-width: 500px;
    width: 100%;
    &__user{
        text-align: left;
        display: flex;
        align-items: center;
        img{
            width: 40px;
            height: 40px;
            border-radius: 20px;
        }
        p{
            font-size: 24px;
            font-weight: bold;
            padding: 10px;
            flex-grow: 1;
        }
        &__icon-close{
            display: none;
            @media only screen and (max-width: 768px) {
                display: inline-block;
            }
        }
    }
    
